.navbar {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  padding: 1.5rem 2rem 1rem 1.5rem;


  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background: var(--color-low);
}

.navbar__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.navbar__logo img {
  width: auto;
  height: 50px;
  object-fit: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

