.bookcall {
  display: flex;
  flex-direction: column;

  background-color: var(--color-high);
  color: var(--color-low);
  height: 100%;
  width: 100%;
}

.bookcall-heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 2rem;
  margin-bottom: 2rem;
}


.bookcall-calendly iframe {
  width: 100%;
  height: 700px;
  border: none;
}



