.video {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;

  background-color: var(--color-low);
  color: var(--color-high);

  gap: 5rem;
}

.nav-place {
  height: 1rem;
}



.video__iframe {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.video__iframe p
{
  font-size: 1.25rem;
  font-weight: 200;
  margin-top: 1rem;
  text-transform: capitalize;
}
.video iframe {
  width: 100%;
  height: 600px;
}




@media screen and (max-width: 2560px) and (min-width: 1500px) {
}

@media screen and (max-width: 1000px) {
  .video iframe {
    height: 400px;
  }
}

@media screen and (max-width: 768px) {
  .video iframe {
    height: 400px;
  }
}

@media screen and (max-width: 700px) {
  .video iframe {
    height: 300px;
  }
}

@media screen and (max-width: 550px) {
  .video iframe {
    width: 100%;
  }
}

