.learn-more
{
    z-index: 3;
}
.learn-more a:hover
{       
    color: var(--color-blue);
    font-weight: 800;
    transition: smooth 2s ease-in-out;
}

.learn-more a
{
    color: var(--color-dark);
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon
{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
}

@media screen and (max-width: 550px) {
    .learn-more a
    {
        font-size: 0.9rem;
    }
    .icon
    {
        font-size: 1.5rem;
    }
}