.VSLFooter
{
    background: var(--color-low)!important;
    color: var(--color-high);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 100%;


    padding: 6rem 3rem 1.5rem 0rem;
}

.vslfooter-left
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    flex: 0.3;

}
.vslfooter-logo
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.vslfooter-logo img {
  width: auto;
  height: 85px;
  object-fit: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

}

.vslfooter-right
{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 1rem;
    flex: 0.7;
}

.disclaimer
{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 1.5rem;
}

.vslfooter-top
{
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    gap: 1rem;
    width: 100%;
}

.vslfooter-top h3
{
    margin-bottom: 1rem;
}

.vslfooter-top a:hover
{
    color: var(--color-blue);
}

@media screen and (max-width: 1024px)
{
    
}

@media screen and (max-width: 768px)
{
    .VSLFooter
    {
        padding: 3rem 2rem 1.5rem 2rem;
    
        flex-direction: column;
        justify-content: center;
        gap: 2rem;
    }

    .vslfooter-left
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;
        flex: 0.3;
    }

    .vslfooter-logo
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;
    }
    .vslfooter-top
    {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        gap: 1rem;
    }
    .vslfooter-bottom
    {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        
    }
    .vslfooter-logo
    {
        align-items: flex-start;
        
    }
    .vslfooter-logo img
    {
        height: 70px;
    }
    
}

@media screen and (max-width: 550px)
{
    .vslfooter-logo img
    {
        height: 60px;
    }
    
}