@import url("https://fonts.googleapis.com/css2?family=manrope:wght@200;300;400;500;600;700;800&display=swap");

:root {
  --font-family: "manrope", sans-serif;
  --link: "https://calendly.com/conor-sdp/30min?month=2024-02";

  --color-high: #000000;
  --color-low: #ffffff;
  --color-lower: rgb(139, 138, 138);
  --color-blue: #49bbeb;
  --color-darkblue: #0d39fd;

  --theme-em-gradient: linear-gradient(90deg, #7209d4, #2832d4 33%,  #00a5b2);
}

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  gap: 2rem;
}

.container-rev {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: flex-start;
  text-align: left;
  gap: 4rem;
}
.heading {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.vsl-heading
{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
  gap: 2rem;
}

.vsl-heading h1
{
  font-size: 2.5rem;
  margin: 0;
  text-transform: capitalize;
}

.vsl-heading h2
{
  font-size: 2rem;
  font-weight: 400;
  
  text-align: center;
  text-transform: capitalize;
}

.heading h1 {
  font-size: 2.5rem;
  margin: 0;
  text-transform: uppercase;
}

.content p {
  text-align: left;
  line-height: 1.75rem;
  font-size: 1.25rem;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 2rem;
  flex: 0.8;
}

.image {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 0.3;
}

.image img {
  width: 20rem;
}

.call-book-btn-white {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;

  padding: 0.75rem 1rem;

  background: var(--color-low);
  color: var(--color-high);

  border-radius: 12px;
  border: 2px solid var(--color-low);

  font-weight: 600;
  font-size: 1rem;

  z-index: 3;
  /*box-shadow: 5px 5px 5px rgba(211, 211, 211, 0.2);*/

  cursor: pointer;
}

.call-book-btn-white:hover {
  color: var(--color-darkblue);
}

.call-book-btn, .guidebtn, .cta {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0.75rem 1rem;

  background: var(--color-high);
  color: var(--color-low);

  border-radius: 12px;
  border: 2px solid var(--color-high);

  font-weight: 400;
  font-size: 1rem;

  /*box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);*/

  cursor: pointer;
}

.guidebtn
{
  gap: 1rem;
}


.guidebtn:hover, .call-book-btn:hover
{
  background-color: var(--color-blue);
  border-color:  var(--color-blue);
  color: var(--color-high);
}



@media screen and (max-width: 2560px) and (min-width: 1500px) {
  .heading h1, .vsl-heading h1{
    font-size: 5rem;
  }

  .vsl-heading h2
  {
    font-size: 3.5rem;
  }

  .content p {
    font-size: 2.5rem;
  }
  .call-book-btn,
  .call-book-btn-white {
    padding: 1.5rem 2rem;
    font-size: 2rem;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
  }
}

@media screen and (max-width: 1024px) {
  .heading h1, .vsl-heading h1{
    font-size: 2.25rem;
  }

  .vsl-heading h2
  {
    font-size: 1.5rem;
  }
  .content p {
    font-size: 1.15rem;
  }
  .image img {
    width: 15rem;
  }
}

@media screen and (max-width: 768px) {
  .heading h1, .vsl-heading h1 {
    font-size: 2rem;
  }

  .content p {
    font-size: 1.15rem;
  }
  .call-book-btn,
  .call-book-btn-white {
    font-size: 0.75rem;
  }

  .container,
  .container-rev {
    flex-direction: column-reverse;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 2rem;
  }

  .image {
    align-items: flex-start;
    justify-content: flex-start;
  }

  .image img {
    width: 8rem;
  }
}

@media screen and (max-width: 550px) {
  .heading h1, .vsl-heading h1 {
    font-size: 1.75rem;
  }
  .content p {
    font-size: 1.1rem;
  }
  .image img
  {
    width: 6rem;
  
  }
}

@media screen and (max-width: 430px) {
  .heading h1 {
    font-size: 1.5rem;
  }

  .vsl-heading h1
  {
    font-size: 1.25rem;
  }

  .vsl-heading h2
  {
    font-size: 1.15rem;
  }
  .call-book-btn,
  .call-book-btn-white {
    padding: 0.5rem 0.75rem;
    font-size: 0.7rem;
    border-radius: 5px;
  }
}
@media screen and (max-width: 400px) {
  .heading h1, .vsl-heading h1 {
    font-size: 1.3rem;
  }
  .content p {
    font-size: 1rem;
  }
}
@media screen and (max-width: 320px) {
  .heading h1, .vsl-heading h1 {
    font-size: 1.25rem;
  }
}
